html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
}

.section {
  padding: 2rem 0;
}

h2 {
  margin: 0 0 5rem 0;
  color: #490011;
  text-align: center;
  font-size: 4rem;
  font-weight: 700;
}
h2::after {
  content: "";
  display: block;
  width: 12rem;
  height: 12rem;
  margin: 0 auto;
  background-image: url(../img/pokeballopen.gif);
  background-repeat: no-repeat;
  background-size: 12rem;
}

.header {
  background-color: #f7cf07;
}
.header__grid {
  display: flex;
  flex-direction: column;
}
.header__bar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.header__logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.header__logo-img {
  width: 5rem;
  height: 5rem;
}
.header__logo-text {
  font-size: 3rem;
  font-weight: 700;
  color: #490011;
}

.header__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .header__nav {
    flex-direction: row;
    padding: 0;
  }
}
.header__nav-link {
  color: #490011;
  font-size: 1.8rem;
  font-weight: 400;
  transition: color 0.3s;
}
.header__nav-link:hover {
  color: #FFFFFF;
}
.header__nav-link--active {
  color: #FFFFFF;
}

.header__pikachu {
  display: none;
}
@media (min-width: 768px) {
  .header__pikachu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 10rem 0;
  }
}
.header__pikachu-text {
  margin: 0;
  color: #FFFFFF;
  font-size: 29rem;
  font-weight: 900;
  position: relative;
  user-select: none;
}
.header__pikachu-img {
  width: 60rem;
  position: absolute;
  right: 30rem;
}

.header__footer {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(1, 1fr);
  align-items: flex-start;
  justify-content: center;
  margin-block: -3rem;
  text-align: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .header__footer {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(5, 1fr);
    text-align: left;
    margin-top: 0;
    justify-content: center;
    margin-block: -3rem;
  }
}
.header__footer-text {
  font-size: 2.5rem;
  font-weight: 700;
  color: #490011;
  user-select: none;
}
.header__footer-button {
  grid-row: 2/3;
  width: 100%;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 400;
  color: #490011;
  padding: 1rem 3rem;
  border: 3px solid #7c001d;
  border-radius: 8px;
  transition: background-color 0.3s;
}
.header__footer-button:hover {
  background-color: #440010;
  color: #FFFFFF;
}

.header__border-img {
  background-color: #FFFFFF;
}

.pokemon__heading {
  margin-top: 0;
  color: #490011;
  text-align: center;
  font-size: 4rem;
  font-weight: 700;
  letter-spacing: 5px;
}
.pokemon__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  gap: 6rem;
}
@media (min-width: 768px) {
  .pokemon__grid {
    grid-template-columns: repeat(4, 1fr);
    justify-content: center;
    gap: 6rem;
  }
}

.pokemon__card {
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 1px 2px 5px 1px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}
.pokemon__card:hover {
  transform: scale(1.2);
}
.pokemon__card-image {
  margin: 0 auto;
  width: 50%;
  transition: transform 0.3s;
}
.pokemon__card-image:hover {
  transform: scale(1.2);
}
.pokemon__card-name {
  text-align: center;
  color: #000;
  font-size: 2rem;
  margin: 2rem 0 0 0;
  text-transform: uppercase;
}
.pokemon__card-description {
  text-align: left;
  font-size: 1.6rem;
  font-weight: bold;
  color: darkorange;
}
.pokemon__card-description--span {
  font-weight: normal;
  color: gray;
  font-size: 1.5rem;
}

.pokemon__button {
  width: 20rem;
  margin: 10rem auto 5rem auto;
}
.pokemon__button a {
  grid-row: 2/3;
  width: 100%;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 400;
  color: red;
  padding: 1rem 3rem;
  border: 3px solid #ff3333;
  border-radius: 8px;
  transition: background-color 0.3s;
}
.pokemon__button a:hover {
  background-color: #fa0000;
  color: #FFFFFF;
}

.pokemon__filter {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 5rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .pokemon__filter {
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    margin-bottom: 5rem;
  }
}
.pokemon__filter-search {
  border: 2px solid darkorange;
  padding: 1rem 3rem;
  outline: none;
  color: gray;
  text-align: center;
}
.pokemon__filter--color {
  border: 2px solid darkorange;
  text-align: center;
  outline: none;
  color: gray;
}

.about {
  margin-top: 5rem;
}
.about__grid {
  display: grid;
}
.about__card {
  display: grid;
  grid-template-rows: 30rem 45rem;
  box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  border-radius: 30px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .about__card {
    display: grid;
    grid-template-columns: 30rem 60rem;
    grid-template-rows: unset;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    border-radius: 30px;
    overflow: hidden;
    height: 38rem;
  }
}
.about__image {
  background-color: #343852;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about__image img {
  margin: 1rem auto;
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
  transition: transform 0.3s;
}
.about__image img:hover {
  transform: scale(1.1);
}
.about__years {
  text-align: center;
  padding: 1rem 2rem;
  border-radius: 10px;
  background-image: linear-gradient(to left, #7d62d4 0%, #e655ae 100%);
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.7rem;
}
.about__info {
  grid-row: 2/3;
  background-color: #343852;
  border-left: 3px solid #3d425e;
}
@media (min-width: 768px) {
  .about__info {
    grid-row: unset;
    grid-column: 2/3;
    background-color: #343852;
    border-left: 3px solid #3d425e;
  }
}
.about__name {
  margin-top: 2rem;
  margin-bottom: 1rem;
  text-align: center;
  text-transform: uppercase;
  font-size: 2.5rem;
  font-weight: 700;
  color: #FFFFFF;
}
.about__description {
  line-height: 2;
  margin-top: 0;
  padding: 1rem 5rem;
  text-align: center;
  color: #abacb2;
  font-size: 1.7rem;
}

.sk-folding-cube {
  margin: 20px auto;
  width: 40px;
  height: 40px;
  position: relative;
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

.sk-folding-cube .sk-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: darkorange;
  -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}

.sk-folding-cube .sk-cube2 {
  -webkit-transform: scale(1.1) rotateZ(90deg);
  transform: scale(1.1) rotateZ(90deg);
}

.sk-folding-cube .sk-cube3 {
  -webkit-transform: scale(1.1) rotateZ(180deg);
  transform: scale(1.1) rotateZ(180deg);
}

.sk-folding-cube .sk-cube4 {
  -webkit-transform: scale(1.1) rotateZ(270deg);
  transform: scale(1.1) rotateZ(270deg);
}

.sk-folding-cube .sk-cube2:before {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.sk-folding-cube .sk-cube3:before {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
}

.sk-folding-cube .sk-cube4:before {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s;
}

@-webkit-keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
@keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
.footer {
  background-color: #000;
  margin-top: 5rem;
}
.footer__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
.footer__heading {
  grid-row: 1/2;
  color: #FFFFFF;
  text-align: center;
  font-size: 3rem;
}
.footer__social-icons {
  grid-row: 2/3;
  display: flex;
  justify-content: space-evenly;
}
.footer__social-icon img {
  width: 5rem;
  height: 5rem;
}